import { useMutation } from "@apollo/client";
import { useState } from "react";
import { REGISTER_USER } from '../graphql/queries';
import { AuthContext } from "../context/auth-context";
import { useContext } from "react";

const RegisterForm = () => {
  const context = useContext(AuthContext);
  const [register, setRegister] = useState({ email: '', password: '' });
  const [adduser, result] = useMutation(REGISTER_USER, {
    onError: (error) => console.log(error.graphQLErrors[0].extensions.exception.errors),
  });
  const SubmitRegisterForm = async (e) => {
    e.preventDefault();
    await adduser({ variables: { email: register.email, password: register.password } });
  };
  return (
    <div className="register-form">
      <form onSubmit={SubmitRegisterForm}>
        <h2>Register</h2>
        <div className="register-form-wrapper">
          <div className="labels">
            <label>Email</label><br />
            <label>Password</label>&nbsp;
          </div>
          <div className="inputs">
            <input onChange={(e) => setRegister({ ...register, email: e.target.value })} name="email" type="email"></input><br />
            <input onChange={(e) => setRegister({ ...register, password: e.target.value })} name="password" type="password"></input>&nbsp;
          </div>
        </div>
        <button type="submit">Submit</button>
      </form>
    </div>
  );
};
export default RegisterForm;