import { AuthContext } from "../context/auth-context";
import { useContext } from "react";

const LogOut = ({client}) => {
  const context = useContext(AuthContext);
  const DoLogOut = () => {
    localStorage.clear()
    context.logout();
    window.location.href = '/';
  }
  return (
    <>
    {DoLogOut()}
    </>
  );
};
export default LogOut;