
import { Link } from "react-router-dom";

function User({ user, tickets }) {
  

  return (
    <div className='user'>
      <Link key={user?.id} to={`/profile/${user?.id}`}><h4>Go to user page</h4></Link><br />
      <div className="personal-info-wrapper">
        <div className="labels">
          <p>Role</p>
          <p>Name</p>
          <p>Email</p>
          <p>Phone number</p>
        </div>
        <div className="personal-info">
          <p>{user?.role}</p>
          <p>{user?.name}&nbsp;</p>
          <p>{user?.email}&nbsp;</p>
          <p>{user?.phone}&nbsp;</p>
        </div>
      </div>
    </div>
  );
};

export default User;