import { useMutation, useQuery } from "@apollo/client";
import { FIND_USER, EDIT_USER, ALL_USERS, REMOVE_USER, FIND_COMMENTS, FIND_TICKETS_BY_USER } from "../graphql/queries";
import { AuthContext } from "../context/auth-context";
import { useContext, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { Ticket } from './tickets';

import {formatDate} from '../utils/helpers';

const Profile = () => {
  const commentQuery = useQuery(FIND_COMMENTS);
  const [removeuser] = useMutation(REMOVE_USER, {
    onError: (err) => { console.log(err.graphQLErrors) },
    refetchQueries: [
      {
        query: ALL_USERS,
      }
    ],
    onCompleted: () => { alert('user deleted') },
  });
  const [edit, setEdit] = useState();
  const [edituser, result] = useMutation(EDIT_USER, {
    onError: (err) => console.log(err.graphQLErrors[0].extensions.exception),
    refetchQueries: [
      FIND_USER
    ]
  });

  const handleUserEdit = async (e) => {
    e.preventDefault();
    console.log('edit user: ', { ...edit })
    await edituser({ variables: { ...edit, id: userId ? userId : user.id } });
    setEditmode(false);
  };

  const EditUserForm = () => {
    return (
      <div className="personal-info">
        <form onSubmit={handleUserEdit}>
          <p>{isAdmin ? (<select defaultValue={query.data.findUser.role} onChange={(e) => setEdit({ ...edit, role: e.target.value })} id="role" name="role">
            <option value="Admin">Admin</option>
            <option value="User">User</option>

          </select>) : query?.data?.findUser?.role}</p>
          <p><input onChange={(e) => setEdit(
            {
              ...edit,
              name: e.target.value
            }
          )} defaultValue={query.data.findUser.name} name="name" type="text"></input></p>
          <p><input onChange={(e) => setEdit({ ...edit, email: e.target.value })} defaultValue={query.data.findUser.email} name="email" type="email"></input></p>
          <p><input onChange={(e) => setEdit({ ...edit, phone: e.target.value })} defaultValue={query.data.findUser.phone} name="phone" type="text"></input></p>&nbsp;
          <button type="submit">Save changes</button>&nbsp;
          {isAdmin ? (
            <button
              onClick={
                async () => {
                  await removeuser(
                    {
                      variables: { id: query.data.findUser.id.toString() }
                    }
                  )
                }
              }
              type="button">Delete user</button>) : ''}
        </form>
      </div>
    );
  };

  var { userId } = useParams();

  const { user, login, logout } = useContext(AuthContext);
  const [editmode, setEditmode] = useState(false);

  const toggleEdit = (e) => {
    e.preventDefault();
    setEditmode(!editmode);
  }

  const query = useQuery(FIND_USER, { variables: { id: userId ? userId : user?.id } });
  console.log(query)
  const isAdmin = user.role === 'Admin';
  const ticketQuery = useQuery(FIND_TICKETS_BY_USER, {
    variables: {
      id: userId ? userId : user?.id
    }
  });

  if (query.loading || commentQuery.loading || ticketQuery.loading) return (<p className="loading">Loading..</p>);
  if (!user) return (<p>Not logged in..</p>);

  return (
    <div className="profile">
      <h2>Profile</h2>
      {(isAdmin || user) ? <button onClick={toggleEdit}>Edit</button> : ''}
      <div className="personal-info-wrapper">
        <div className="labels">
          <p>Role</p>
          <p>Name</p>
          <p>Email</p>
          <p>Phone number</p>
        </div>
        {editmode ? EditUserForm() : (<div className="personal-info">
          <p>{query?.data?.findUser?.role}</p>
          <p>{query?.data?.findUser?.name}&nbsp;</p>
          <p>{query?.data?.findUser?.email}&nbsp;</p>
          <p>{query?.data?.findUser?.phone}&nbsp;</p>
        </div>)}
      </div>
      <div className="ticket-list-wrapper">
        <h4>Tickets</h4>
        <ul>
          {query?.data?.findUser?.tickets.map(ticket => <li><Ticket ticket={ticket} /></li>)}
        </ul>
      </div>
    </div>
  );
};
export default Profile;