const { gql } = require('@apollo/client')

export const ALL_USERS = gql`
  query {
    allUsers  {
      email
      role
      name
      phone
      id
      tickets {
        id
        content {
          title
        }
        order
        date
      }
    }
  }
`
export const FIND_TICKETS_BY_USER = gql`
    query ($id: String){
      ticketsByUser(id: $id) {
        id
        content {
          title
          text
        }
        date
      }
  }
`;

export const ALL_TICKETS = gql`
  query {
    allTickets {
      id
      status
      content {
        title
        text
      }
      assigned {
        id
        email
      }
      order
      date
      author {
        id
        email
      }
      comments {
        id
        text
        id
      }
    }
  }
`

export const ME = gql`
  query {
    me {
      email
      role
      id
    }
  }
`
export const FIND_COMMENT = gql`
  query ($id: String!) {
    comment(id: $id) {
      author
      {
        id
        email
      }
      id
      text
      date
    }
  }
`;
export const FIND_COMMENTS = gql`
  query {
    comments {
      author {
        id
        email
      }
      id
      text
      date
      ticket {
        id
      }
    }
  }
`;

export const FIND_TICKET = gql`
  query ($id: String!) {
    ticket(id: $id) {
      id
      author {
        id
        email
      }
      date
      order
      status
      content {
        title
        text
      }
      assigned {
        id
      }
      comments {
        author {
          id
          email
        }
        text
        id
      }
    }
  }
`
export const FIND_USER = gql`
  query ($id: String!) {
    findUser(id: $id) {
      id
      name
      email
      phone
      role
      tickets {
        id
        author {
          id
          email
        }
        date
        content {
          title
          text
        }
        order
      }
    }
  }
`

export const ADD_USER_TO_TICKET = gql`
  mutation addUserToTicket($id: String, $userid: String) {
    addUserToTicket(id: $id, userid: $userid) {
      id
    }
  }
`;
export const REMOVE_USER_FROM_TICKET = gql`
  mutation removeUserFromTicket($id: String, $uid: String) {
    removeUserFromTicket(id: $id, uid: $uid) {
      id
    }
  }
`;
export const REGISTER_USER = gql`
  mutation addUser($email: String!, $password: String!) {
    addUser(email: $email, password: $password) {
      email
      id
    }
  }
`

export const EDIT_USER = gql`
 mutation editUser($name: String, $email: String, $phone: String, $role: String, $id: String!) {
   editUser(name: $name, email: $email, phone: $phone, role: $role, id: $id) {
     name
     email
     id
     phone
     role
   }
 }
`
export const EDIT_TICKET = gql`
 mutation editTicket($id: String, $status: String) {
  editTicket(id: $id, status: $status) {
    id
   }
 }
`;
export const ADD_COMMENT = gql`
 mutation addComment($text: String, $uid: String, $id: String) {
  addComment(text: $text, uid: $uid, id: $id) {
    id
   }
 }
`

export const LOGIN = gql`
  mutation login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      value
    }
  }`

export const REMOVE_TICKET = gql`
  mutation removeTicket($id: String!) {
    removeTicket(id: $id) {
      id
    }
  }
`
export const REMOVE_COMMENT = gql`
  mutation removeComment($id: String, $cid: String) {
    removeComment(id: $id, cid: $cid) {
      id
    }
  }
`
export const REMOVE_USER = gql`
  mutation removeUser($id: String!) {
    removeUser(id: $id) {
      id
    }
  }
`

export const ADD_TICKET = gql`
  mutation addTicket($content: TicketContentInput) {
    addTicket(content: $content) {
      id
      order
    }
  }`