import './App.css';
import Tickets from './components/tickets';
import Header from './components/header';
import ReportTicketForm from './components/ReportTicketForm';
import Notification from './components/notification';
import Footer from './components/footer';
import { Routes, Route } from 'react-router-dom'
import LogInForm from './components/LogInForm';
import RegisterForm from './components/RegisterForm';
import Profile from './components/profile';
import { useEffect, useState } from 'react';
import { useQuery } from '@apollo/client';
import { ALL_TICKETS, ME } from './graphql/queries'
import LogOut from './components/LogOut';
import Manage from './components/manage';

function Database({props}) {
  return (
    <>
    {props}
    </>
  );
}

function App() {
  const user = useQuery(ME);
  const ticketQuery = useQuery(ALL_TICKETS);

  if (user.loading || ticketQuery.loading) return <p>Loading...</p>
  return (
    <div className="main">
      <header className="header">
        <Header />
      </header>
      <div className='content'>
          <Routes>
            <Route path='/add' element={<ReportTicketForm />} />
            <Route path='/tickets' element={<Tickets  />} />
            <Route path='/tickets/:ticketId' element={<Tickets />} />
            <Route path='/login' element={<LogInForm />} />
            <Route path='/register' element={<RegisterForm />} />
            <Route path='/profile' element={<Profile />} />
            <Route path='/profile/:userId' element={<Profile />} />
            <Route path='/logout' element={<LogOut />} />
            <Route path='/manage/*' element={<Manage />} />

          </Routes>
      </div>
      <Footer />
    </div>
  );
}

export default App;
