import { useMutation } from "@apollo/client";
import { useState, useEffect, useContext } from "react";
import { LOGIN } from "../graphql/queries";
import { AuthContext } from "../context/auth-context";

const LogInForm = () => {
  const { user, logout }= useContext(AuthContext);
  const [logininfo, setLogininfo] = useState();
  const [token, setToken] = useState();

  const [login, result] = useMutation(LOGIN, {
    onError: (e) => console.log(e)
  });

  useEffect(() => {
    if (result.data) {
      const token = result.data.login.value
      console.log('token', result.data.login)
      if (token === 'user not found' || token === 'wrong password') return;
      localStorage.setItem('token', token)
      login(token);
    }
  }, [result.data]) // eslint-disable-line

  const HandleLogin = async (e) => {
    e.preventDefault();
    await login({variables: {email: logininfo.email, password: logininfo.password}});
    window.location.href = '/tickets';
  };

  return (
    <div className="login-form">

      <form onSubmit={HandleLogin}>
        <h2>Login</h2>

        <div className="login-wrapper">

          <div className="login-labels">
            <label>Email</label>
            <br />
            <label>Password</label>&nbsp;
          </div>

          <div className="login-inputs">
            <input onChange={(e) => setLogininfo({ ...logininfo, email: e.target.value })} type="email"></input>
            <br />
            <input onChange={(e) => setLogininfo({ ...logininfo, password: e.target.value })} type="password"></input> </div>
        </div>

        <button type="submit">Submit</button>
      </form>

    </div>
  )
};
export default LogInForm;