import { useQuery } from '@apollo/client';
import React, { useState } from 'react';
import { ALL_TICKETS } from '../graphql/queries';
import { Ticket } from './tickets';

function TicketList() {
  var { data, error, loading } = useQuery(ALL_TICKETS);
  const [tickets, setTickets] = useState();
  if (loading) return <p>Loading...</p>
  if(!data) return <p>Problem with thö sörvör..</p>
  if(error) return <p>Errorrii puckaa {error}</p>
  return (
    <div>
      <h4>[ Hide comments ]</h4>
      {data.allTickets.map(ticket => <Ticket ticket={ticket} />)}
    </div>
  )
}

export default TicketList