import React from 'react'
function formatDate(data) {
  let date = new Date(data);
  // HH:mm dd.mm.YYYY
  return `${date.getHours()}:${date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()} ${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;
};
function Comment({ comment }) {
  if (!comment) return <div></div>
  return (
    <div className='comment-wrapper'>
      <div className='comment-id'>#{comment?.id}</div>
      <div className='comment-text'>{comment?.text}</div>
      <div className='comment-author'>by {comment?.author?.id} @ {formatDate(comment?.date)}</div>
    </div>
  );
};

export default Comment