import {
  ALL_TICKETS,
  ADD_COMMENT,
  FIND_TICKET,
  REMOVE_COMMENT,
  EDIT_TICKET,
  ALL_USERS,
  REMOVE_TICKET,
  ADD_USER_TO_TICKET,
  REMOVE_USER_FROM_TICKET,
  FIND_COMMENT,
  FIND_COMMENTS
} from '../graphql/queries';
import {
  useQuery,
  useMutation
} from '@apollo/client';
import {
  useEffect,
  useState,
  useContext
} from 'react';
import {
  Link,
  useParams
} from 'react-router-dom';
import { AuthContext } from '../context/auth-context';
import { TicketContext } from '../context/ticket-context'
import Comments from './Comments';

let formatDate = (data) => {
  let date = new Date(data);
  // HH:mm dd.mm.YYYY
  return `${date.getHours()}:${date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()} ${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;
}
const TicketSearch = ({ filter }) => {
  return (
    <div className="ticket-search">
      <label>Ticket# or search by words&nbsp;</label><input onChange={filter} type="text" name="filter"></input>
    </div>
  );
};
export const Ticket = ({ ticket, comments }) => {

  const userQuery = useQuery(ALL_USERS);
  const { user } = useContext(AuthContext);
  const [editmode, setEditmode] = useState(false);
  const [desc, setDesc] = useState('');
  const [status, setStatus] = useState(ticket.status);
  const [comment, setComment] = useState('');
  const [id, setId] = useState(ticket.id.toString());
  const [removeTicket] = useMutation(REMOVE_TICKET, {
    refetchQueries: [{
      query: ALL_TICKETS
    }]
  });
  const [removeUserFromTicket] = useMutation(REMOVE_USER_FROM_TICKET, {
    onError: (err) => console.log(err),
    refetchQueries: [{
      query: FIND_TICKET,
      variables: {
        id: id
      }
    }]
  });
  const [addHandlerToTicket, resto] = useMutation(ADD_USER_TO_TICKET, {
    onError: (err) => console.log(err),
    refetchQueries: [{
      query: FIND_TICKET,
      variables: {
        id: id
      }
    }],
  });

  const [removeComment] = useMutation(REMOVE_COMMENT, {
    onError: (error) => {
      console.log(error.graphQLErrors[0].extensions.exception)
    },
    refetchQueries: [{
      query: FIND_TICKET,
      variables: { id: id }
    }]
  });
  const [editTicket] = useMutation(EDIT_TICKET, {
    onError: (error) => console.log(error),
    refetchQueries: [{
      query: FIND_TICKET,
      variables: { id: id },
    }],
  });
  const [addComment, result] = useMutation(ADD_COMMENT, {
    onError: (error) => {
      console.log(error.message);
    },
    refetchQueries: [{
      query: FIND_TICKET,
      variables: { id: id }
    }],
  });

  const toggleEditmode = e => {
    e.preventDefault();
    setEditmode(!editmode);
  }

  const handleCommentSend = async (event) => {
    event.preventDefault();
    await addComment({ variables: { id: id, text: comment, uid: (user ? user?.id : 'unregistered') } })
  };

  const handleCommentChange = e => {
    e.preventDefault();
    setComment(e.target.value);
  };


  const addUserToTicket = async (userid) => {
    await addHandlerToTicket({
      variables: {
        id: id,
        userid: userid
      }
    });
  };
  const AdminMenu = () => {
    return (
      <>
        <div>

          <ul>
            <h4>Assigned workers</h4>
            {ticket.assigned?.map(assigned => (
              <li key={assigned.id}>{assigned.email}
                <button key={assigned.id}
                  type='button'
                  onClick={async () => {
                    await removeUserFromTicket({
                      variables: {
                        id: ticket.id,
                        uid: assigned.id
                      }
                    })
                  }}>X</button></li>
            ))}
          </ul>
          {userQuery.loading ? <p className="loading">loading...</p> : ''}
          <form>
            <select>
              {userQuery.data.allUsers.map(user => (
                <option key={user.id} onClickCapture={() => {
                  addUserToTicket(user.id);
                }}>{user.email}</option>
              ))}
            </select>
          </form>
        </div>
        <button form='ticket-form' type='submit'>Save Ticket&nbsp;</button>
        <button onClick={() => removeTicket({ variables: { id: id } })} type='button'>Delete Ticket&nbsp;</button>
        <button onClick={toggleEditmode}>Cancel edit</button>
      </>
    );
  };

  const SubmitTicketChanges = async (e) => {
    e.preventDefault();
    await editTicket({
      variables:
      {
        id,
        status
      }
    });
    setEditmode(false);

  }
  return (
    <div className='ticket' key={ticket.id.toString()}><div className='ticket-header'>
      <Link to={`/tickets/${ticket.id}`}><span className="ticket-title">#{ticket.order} {ticket.content.title}</span> @ {formatDate(ticket.date)}&nbsp;</Link>
      {user?.role === 'Admin' ? <button onClick={toggleEditmode}>Edit</button> : ''}</div>
      {editmode
        ?
        <form id="ticket-form" onSubmit={SubmitTicketChanges}><br/>Status:&nbsp;
          <select onChange={(e) => setStatus(e.target.value)} defaultValue={ticket.status} defaultChecked={ticket.status}>
            <option value="pending">Pending</option>
            <option value="busy">Busy</option>
            <option value="not-busy">Not Busy</option>
            <option value="solved">Solved</option>
          </select><br />
          {AdminMenu()}
        </form>
        :
        <p>Status:&nbsp;{ticket.status}</p>}
      <p className='ticket-description'>{ticket.content.text}</p>
      {comments ? (<Comments ticket={ticket} />) : ''}
    </div>
  );
};

const Tickets = ({tickets}) => {
  var { ticketId } = useParams();
  const [filter, setFilter] = useState('');
  const ticketQuery = useQuery(ALL_TICKETS);
  if (ticketQuery.loading) return <p className='loading'>Loading...</p>
  if(!ticketQuery.data) return <p>Problem with thö sörvör..</p>
  if(ticketQuery.error) return <p>Errorrii puckaa {ticketQuery.error}</p>
  const handleFilter = e => {
    e.preventDefault()
    setFilter(e.target.value);
  };
  var results = ticketQuery?.data?.allTickets.filter(ticket =>
    ticketId ? ticket.id === ticketId
      : (ticket?.content?.author?.toLowerCase().includes(filter)
        || ticket.content.text.toLowerCase().includes(filter)
        || ticket.order.toString().includes(filter)))
    .reverse();
  const isBrowsing = (window.location.pathname.substring(window.location.pathname.lastIndexOf('/')).length < 9);
  return (
    <div className="tickets">
      <h2>Tickets</h2>
      {isBrowsing ? <TicketSearch filter={handleFilter} /> : ''}
      {results?.map(ticket => <Ticket key={ticket.id} ticket={ticket} comments={ticketId ? true : false} />)}
    </div>
  );
};
export default Tickets;