import { useMutation } from '@apollo/client';
import React, { useContext, useState } from 'react'
import {ADD_COMMENT, FIND_COMMENT, FIND_COMMENTS} from '../graphql/queries';
import { AuthContext } from '../context/auth-context';

function AddComment({ticket, add}) {
  async function initComment(e){
    e.preventDefault();
    await add({variables: comment});
  };
  const { user } = useContext(AuthContext);
  
  const [ comment, setComment] = useState({ uid: user ? user.id : 'unregistered', text: '', id: ticket.id });
  return (
    <div>
      <form id='comment-form' onSubmit={(e) => initComment(e)} className="comment-form">
      <input className='comment-input' onSubmit="reset" onChange={(e) => setComment({...comment, text: e.target.value})} type="text" name="comment"></input>
      <br />
      <button form='comment-form' type="submit">Add comment</button>
      </form>
    </div>
  );
};

export default AddComment