import { useMutation, useQuery } from '@apollo/client';
import React, { useState } from 'react'
import { ADD_COMMENT, FIND_COMMENTS } from '../graphql/queries';
import AddComment from './AddComment';
import Comment from './Comment';

function Comments({ ticket }) {
  const [addComment] = useMutation(ADD_COMMENT, {
    onError: (err) => console.log(err.message),
    refetchQueries: [{
      query: FIND_COMMENTS
    }]
  });
  const { data, error, loading } = useQuery(FIND_COMMENTS, {
    onError: (err) => console.log(err),
  });
  if (loading) return (<div>loading..</div>)
  if (!data) return (<div>error wit da srv</div>)
  if (error) return (<div>{error}</div>)
  const comments = data.comments.filter(comment => comment.ticket.id === ticket.id)
  return (
    <div className='comments wrapper'>
      <div className='comments show'>
        <button type='button'>Show Comments</button>
      </div>
      <div className='comments'>
        {comments.map(comment => (<Comment key={comment.id} comment={comment} />))}
      </div>
      <div className='comments add'>
        <AddComment add={addComment} ticket={ticket} />
      </div>
    </div>
  )
};
export default Comments;