import { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../context/auth-context";

const ShowRegisterAndLoginButtons = () => {
  return (
    <>
      <Link to='/register'><div className="link">Register</div></Link>
      <Link to='/login'><div className="link">Log in</div></Link>
    </>
  )
}

const ShowLogoutButton = (logout, user) => {
  return (
    <>
    {user.role === 'Admin' ? <Link to='/manage'><div className="link">Manage</div></Link> : ''}
    <Link to='/profile'><div className="link">Profile</div></Link>
    <Link to='/' onClick={logout}><div className="link">Log out</div></Link>
    </>
  )
}
const Header = () => {
  const {user, logout} = useContext(AuthContext);
  return (
    <div className="header">
      <Link to='/'><h1>~ CORPORATiON ~</h1></Link>
      <h2>Helpdesk</h2>
      <nav className="topnav">
        <div className="navlink">
          <Link to='/tickets'><div className="link">Browse tickets</div></Link>
          <Link to='/add'><div className="link">Add ticket</div></Link>
          {!user ? ShowRegisterAndLoginButtons() : ShowLogoutButton(logout, user)}
        </div>
      </nav>
    </div>
  )
}
export default Header;