import { useState } from 'react';
import { ADD_TICKET } from '../graphql/queries';
import { useMutation } from '@apollo/client';
import Notification from './notification';

const ReportTicketForm = ({me}) => {
  const [ticket, result] = useMutation(ADD_TICKET, {
    onError: (error) => {
      alert(error.graphQLErrors[0].message);
    }
  });
  const [description, setDescription] = useState('');
  const [title, setTitle] = useState('');
  const [author, setAuthor] = useState(me?.id);

  const handleChange = e => {
    e.preventDefault();
    setDescription(e.target.value);
  }

  const handleTitle = e => {
    e.preventDefault();
    setTitle(e.target.value);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const Ticket = { content: { author: author, title: title, text: description }};
    var res = await ticket({variables: Ticket});
    if (res.errors) {
      return res;
    }
    window.location.href = `/tickets/${res.data.addTicket.id}`;
    return res;
  }
  
  return (
    <div className="ticket-form">
      <h2>Add ticket</h2>
      <form onSubmit={handleSubmit}>
        <label>Title&nbsp;</label><input className='ticket-input-title' onChange={handleTitle}></input><br />
        <label>Subscripe your problems with software</label>
        <textarea className='ticket-text' onChange={handleChange}></textarea><br />
        <button type="submit">Submit ticket</button>
        <Notification message="dsadsa" />
      </form>
    </div>
    )
}

export default ReportTicketForm;